<template>
    <Layout>
        <Tabs class="abc" :data-source="typeList" class-prefix="type" @update:value="onUpdateType"
              v-model:values="this.type"/>
        <div class="charts-wrapper" ref="chartWrapper">
            <Charts class="charts" :option="chartOptions"/>
        </div>

        <ol v-if="groupedList.length>0">
            <li v-for="(group,index) in groupedList" :key="index">
                <h3 class="title">{{beautify(group.title)}} <span>￥{{group.total}}</span> </h3>
                <ol>
                    <li class="record" v-for="item in group.items" :key="item.id">
                       <span>{{tagString(item.tags)}}</span>
                        <span class="notes">{{item.formItem}}</span>
                        <span>￥{{item.amount}}</span>
                    </li>
                </ol>
            </li>

        </ol>
        <div v-else class="noResult">
            目前没有相关记录!
        </div>

    </Layout>
</template>

<script>
    import Tabs from "@/components/Tabs";
    import intervalList from "@/constants/intervalList";
    import recordTypeList from "@/constants/recordTypeList";
    import dayJs from "dayjs";
    import clone from "@/lib/clone";
    import Charts from "@/components/Charts";
    import _ from 'lodash'

    export default {
        name: "Count",
        data() {
            return {
                type: '-',
                interval: 'day',
                intervalList: intervalList,
                typeList: recordTypeList
            }
        },
        components: {Charts, Tabs},
        computed: {
            keyValueList(){
                const today=new Date()
                const array=[]
                for(let i=0;i<=29;i++){
                    const dateString=dayJs(today.toISOString()).subtract(i,'day').format('YYYY-MM-DD')
                    const found=_.find(this.groupedList,{title:dateString})
                    array.push({key:dateString,value:found?found.total:0})
                }
                array.sort((a,b)=>{
                    if(a.key>b.key){
                        return -1
                    }else if(a.key===b.key){
                        return 0
                    }else{
                        return -1
                    }
                })
                return array
            },
            chartOptions(){
                const keys=this.keyValueList.map(item=>dayJs(item.key).format('YYYY-M-D'))
                const values=this.keyValueList.map(item=>item.value)
                return {
                    grid:{
                        left:0,
                        right:0
                    },
                    xAxis: {
                        type: 'category',
                        data:keys,
                        axisTick:{alignWithLabel:true},
                        axisLine:{
                            lineStyle:{
                                color:'#666'
                            }
                        },
                        axisLabel:{
                            formatter: function (value) {
                                return value.substring(5);
                            }
                        }
                    },
                    yAxis: {
                        type: 'value',
                        show: false
                    },
                    tooltip:{
                        show:true,
                        triggerOn:'click',
                        position:'top',
                        formatter: '{c}'
                    },
                    series: [
                        {
                            data: values,
                            symbol:'circle',
                            symbolSize:12,
                            itemStyle:{
                                color:'#666'
                            },
                            type: 'line'
                        }
                    ]
                }
            },
            groupedList() {
                const {recordList} = this

                const newList=clone(recordList).filter(r=>r.type===this.type).sort((a,b)=>dayJs(b.created).valueOf()-dayJs(a.created).valueOf())
                if(newList.length===0){return []}
                const result=[{title:dayJs(newList[0].created).format('YYYY-MM-DD'),items:[newList[0]]}]
                for(let i=1;i<newList.length;i++){
                    const current=newList[i]
                    const last=result[result.length-1]
                    if(dayJs(last.title).isSame(dayJs(current.created,),'day')){
                        last.items.push(current)
                    }else{
                        result.push({title: dayJs(current.created).format('YYYY-MM-DD'),items:[current]})
                    }
                }
                result.map(group=>{
                    group.total=group.items.reduce((sum,item)=>{
                        return sum+item.amount},0)
                })
                return result
            },
            recordList() {
                return this.$store.state.recordList;
            }
        },
        created() {
            this.$store.commit('fetchRecords')
        },
        mounted(){
            const div=this.$refs.chartWrapper
            div.scrollLeft=div.scrollWidth
        },
        methods: {
            beautify(string){
                const day=dayJs(string)
                const now=dayJs()
                if(day.isSame(now.valueOf(),"day")){
                    return '今天'
                }else if(day.isSame(now.subtract(1,'day'),"day")){
                    return  '昨天'
                }else if(day.isSame(now.subtract(2,'day'),"day")){
                    return '前天'
                }else if(day.isSame(now,'year')){
                    return day.format('M月D日')
                }else{
                    return day.format('YYYY年M月D日')
                }

            },
            tagString(tags){
                if(tags.length!==0){
                    return tags.join("，")
                }else {
                    return '—'
                }

            },
            onUpdateType(value) {
                this.type = value
            },
        }
    }
</script>
<style lang="scss" scoped>
    %item{
        padding:0 16px;
        min-height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    :deep(.type-item) {
        background: white;
        &.selected {
            background: #c4c4c4;
        }
    }
    .noResult{
        text-align: center;
        padding: 40px;
    }
    .title{
        @extend %item
    }
    .record{
        background: white;
        @extend %item

    }
    .notes{
        margin-right: auto;
        margin-left: 16px;
        color: #999999;
    }
    .charts{
        width: 430%;
        &-wrapper{
            overflow: auto;
            &::-webkit-scrollbar {
            display: none;
            }
        }
    }
</style>