<template>
    <div ref="wrapper" class="wrapper"></div>
</template>

<script lang="js">
    import * as echarts from 'echarts'

    export default {
        name: 'Charts',
        props:['option'],
        mounted(){
            this.chart=echarts.init(this.$refs.wrapper,'light')
            this.chart.setOption(this.option)
        },
        watch:{
            option(newValue){
                this.chart.setOption(newValue)
            }
        }

    }
</script>

<style lang="scss" scoped>
    .wrapper{

        height: 400px;
    }
</style>